@use "./variables";
@use "./darkmode";
@use "./custom";
@use "./mixin" as mx;
@use "./animation";
@use "./loaders";
@use "./functions" as fn;
@use "./dashboards";
@use "./admin";

* {
  // outline: 2px solid green;
  // margin: 0;
  // padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
*::before,
*::after {
  box-sizing: inherit;
}

.popularList,
.featured_container {
  &::-webkit-scrollbar {
    width: 0px;
    height: 0px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }
}

.nobar {
  &.null {
    &::-webkit-scrollbar {
      width: 0px;
      height: 0px;
    }
  }
  &::-webkit-scrollbar {
    width: 1px;
    height: 1px;
  }

  &::-webkit-scrollbar-thumb {
    background: fn.color("lightWheat");
    border-radius: 30px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: fn.color("wheat");
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }
}

body {
  -webkit-overflow-scrolling: touch;
  overflow-y: auto;
  overflow-anchor: none;
  position: relative;
  width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
  font-family: fn.font("list");
  @include mx.flex(column, flex-start, flex-start);
}

main {
  @extend body;
  position: relative;
}

.backdrop {
  // @media (max-width: 800px) {
    position: fixed;
    min-width: 100%;
    min-height: 100vh;
    z-index: -100;
    background-color: rgba(255, 255, 255, 0);
     @include mx.transition(300ms, ease-out, background-color);
    &.active {
      background-color: rgba(20, 20, 20, 0.258);
      z-index: 96;
      @include mx.transition(300ms, ease-in);
    }
  // }
}

// nav

.nav {
  position: absolute;
  font-family: fn.font(list);
  background-color: fn.color("white");
  z-index: 103;
  width: 100%;
  min-height: 60px;
  padding-block: 6px;
  padding-inline: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  @include mx.transition(300ms, ease-out);
  @include mx.flex(row, center, space-between);
  a:has(.logo) {
    color: rgb(8, 116, 199);
    text-decoration: none;
  }
  .logo {
    color: rgb(8, 116, 199);
    font-size: 30px;
    font-weight: bold;
    font-family: monospace;
  }
  .profilePic {
    cursor: pointer;
    border-radius: 50%;
  }
  .nav_items {
    gap: 20px;
    @include mx.flex(row, center, center);
    a:hover {
      .link_line,
      span {
        &:hover {
          &::after {
            opacity: 100%;
          }
          @include mx.transition(400ms, ease-out);
        }
      }
    }
    .link_line,
    span {
      position: relative;
      text-decoration: none;
      font-size: 16px;
      color: fn.color("lightDark");
    }
    // mobile footer nav
    @media (max-width: 767.9px) {
      z-index: 103;
      background-color: rgb(255, 255, 255);
      position: fixed;
      bottom: 0%;
      left: 0;
      right: 0;
      height: 76px;
      width: 100%;
      border-top: 2px solid var(--color-navBorderColor);
      // box-shadow: 2px 4px 10px rgba(20, 19, 19, 0.176);
      border-radius: 2px 2px 0 0;
      padding-top: auto;
      padding-bottom: 0;
      gap: 40px;
      .link_line,
      span {
        font-size: 12px;
      }
      img {
        margin-top: 20px;
        padding-left: -20px;
      }
      @include mx.flex(row, center, center);
      @supports (-moz-appearance: none) {
        bottom: auto;
        top: calc(100vh - 76px);
      }
    }
  }
}

.activeNav {
  .toggle_nav {
    position: relative;
    .toggle_items:nth-child(1) {
      transform: rotate(-45deg);
      translate: 0 5px;
      @include mx.transition(300ms, ease-out);
    }
    .toggle_items:nth-child(3) {
      transform: rotate(45deg);
      translate: 0 -5px;
      @include mx.transition(300ms, ease-out);
    }
    .toggle_items:nth-child(2) {
      position: absolute;
      width: 45px;
      height: 45px;
      padding: 20px;
      right: -2.5px;
      border-radius: 50%;
      background-color: transparent;
      opacity: 30%;
      border: 4px dashed fn.color("wheat");
      animation: rollBorder 5s ease-in-out infinite;
      @include mx.transition(300ms);
    }
  }
}

.toggle_nav {
  gap: 5px;
  cursor: pointer;
  @include mx.flex(column, center, center);
  .toggle_items {
    width: 40px;
    height: 3.3px;
    border-radius: 100px;
    transform-origin: center;
    @include mx.transition(300ms, ease-out, translate);
    &:nth-child(1),
    &:nth-child(3) {
      @include mx.transition(300ms, ease-in, translate);
    }
  }
}

.fixedNav {
  position: fixed;
  animation: comedown 400ms;
  @include mx.transition(400ms, ease-out);
}

// footer

.footer {
  @extend .nav;

  border-top: 1px solid grey;
  position: relative;
  margin-top: auto;
  color: white;
  background-color: fn.color("darkblue");
  padding-bottom: 80px;
  z-index: 101;
  @include mx.flex(column, center, flex-start);
  .logo {
    position: relative;
    width: 100%;
    text-align: center;
    color: fn.color("white");
    &::after,
    &::before {
      content: "";
      position: absolute;
      border-radius: 2px;
      width: 40%;
      height: 4px;
      background-color: fn.color("white");
      right: 0;
    }
    &::before {
      left: 0;
    }
  }

  .footer_items_container {
    gap: 8px;
    width: 90%;
    margin-block: 10px;
    margin-inline: auto;
    @include mx.flex(column, flex-start, flex-start);
    @include mx.space(0);
    .footer_items {
      @include mx.flex(row, center, center);
      a {
        text-decoration: none;
        color: fn.color("white");
      }
    }
  }
  .copyright {
    margin-top: auto;
    text-align: center;
  }
}
footer.hide {
  display: none;
}

.goUp {
  z-index: 100;
  position: fixed;
  cursor: pointer;
  padding: 3.1px;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  bottom: 5%;
  right: 2.5%;
  isolation: isolate;
  animation: blink 500ms alternate infinite;
  background-color: fn.color("darkblue");
  @include mx.center();
  @media (max-width: 767.9px) {
    top: auto;
    bottom: clamp(14%, 15%, 12.6%);
  }
  .clickable {
    animation: none;
    border-radius: 50%;
    @include mx.center();
  }
  img {
    border-radius: 50%;
  }
  @media (min-width: 1000px) {
    right: 1%;
  }
}


// card

.card_list {
  margin-top: 8px;
  background-color: fn.color("ghost");
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(310px, max-content));
  align-items: center;
  justify-items: center;
  justify-content: center;
  column-gap: 6px;
  row-gap: 0;
  justify-items: center;
  width: 98%;
  border-radius: 20px;
  margin-inline: auto;
  padding-inline: 2px;
}
.card {
  position: relative;
  font-size: 18px;
  margin: 24px 15px;
  background-color: rgb(255, 255, 255);
  width: 310px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  cursor: pointer;
  border-radius: 5px 5px 10px 10px;
  padding-top: 6px;
  padding-bottom: 10px;
  @include mx.flex(column, center, flex-start);
  @include mx.transition(300ms, ease-out,transform);
  &.blankSlate {
    margin:0px ;
    padding-bottom: 9px;
  }
  a {
    width: 100%;
    height: 100%;
    @include mx.flex(column, center, flex-start);
  }
  &::after {
    content: "";
    border: 2px solid transparent;
    border-radius: 0 0 10px 10px;
    position: absolute;
    bottom: 0;
    right: 0;
    transition: all 300ms ease;
    transform-origin: center;
    z-index: 0;
    width: 0%;
    height: 0;
    // height: 20%;
    background-color: transparent;
    @include mx.transition(300ms, ease-out);
  }
  &:hover {
    transform: scale(0.99);
    @include mx.transition(200ms, ease-in, transform);

    &::after {
      background-color: fn.color("darkblue");
      width: 100%;
      height: 0;
      animation: wobbleEffect 0.6s linear;
    }
    .houseImg {
      z-index: 1;
      @include mx.transition(300ms, ease-in, background);
      &::after {
        content: "";
        position: absolute;
        top: 0%;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: black;
        background: linear-gradient(to top, rgba(0, 0, 0, 0.3), transparent);
        border-radius: inherit;
        @include mx.transition(300ms, ease-in);
      }
    }
  }
  .houseImg {
    position: relative;

 width: 306.5px;
    padding-top: 66.25%;
    margin-top: -14px;
    margin-inline: auto;
    border-radius: 10px;
    @include mx.transition(300ms, ease-out);
    img {
      padding: relative;
      border-radius: 0 0 10px 10px;
    }
  }
  .body {
    z-index: 1;
    overflow: hidden;
    position: relative;
    background-color: #feffff;
    padding-inline: 12px;
    padding-top: 10px;
    padding-bottom: 26px;
    gap: 8px;
    margin-top: 8px;
    border-radius: 20px;
    width: 96%;
    @include mx.flex(column, flex-start, flex-start);
    @include mx.shadow(0.1px, 0.5px);
    .location {
      font-size: 24px;
      font-weight: bold;
    }
    .address {
      position: relative;
      margin-top: -6px;
      opacity: 60%;
      font-size: 15px;
      width: 100%;
      &:after {
        content: "";
        opacity: 100%;
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: fn.color("wheat");
        border-radius: 20px;
        bottom: -50%;
        left: 0%;
      }
    }

    .home_details {
      width: 90%;
      gap: 6px;
      margin-top: 20px;
      margin-inline: auto;
      @include mx.flex(row, center, space-evenly);
      div {
        gap: 6px;
        @include mx.flex(row, center);
      }
    }
 
    .agent {
      margin-top: 4px;
      margin-inline: auto;

      gap: 6px;
      @include mx.flex(row, center, flex-start);
    }
  }
  .tag {
    z-index: inherit;
    text-transform: capitalize;
    color: fn.color("white");
    position: absolute;
    top: -1%;
    left: 3%;
    background-color: rgb(23, 146, 23);
    @include mx.center();
    &.rented {
      background-color: fn.color("wheat");
    }
    &.no_absolute {
      position: relative;
    }
  }

}
.agentCardName {
  color: fn.color("darkblue");
  // width: auto;
  width: 40px;
  overflow-wrap: break-word;
}

.popularCard {
  cursor: pointer;
  @include mx.transition(300ms, ease-out);

  &:hover {
    transform: scale(0.98);
    @include mx.transition(300ms, ease-in);
    .h-30 {
      position: relative;
      display: block;
      &::after {
        content: "";
        z-index: 1;
        position: absolute;
        top: 0%;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: black;
        background: linear-gradient(to top, rgba(0, 0, 0, 0.384), transparent);
        border-radius: inherit;
        @include mx.transition(300ms, ease-in);
      }
    }
  }
}

// featured content

.featured {
  background-color: fn.color("darkblue");
}

.featured_container {
  .agentCardName {
    margin-left: 16px;
    gap: 8px;
    @include mx.flex(row, center, flex-start);
  }

  .card {
    margin-bottom: 10px;
    border-radius: 20px;
    margin-inline: auto;
    width: 320px;
    @supports (-moz-appearance: none) {
      width: 300px;
    }
    .body {
      padding-top: 0px;
      padding-bottom: 16px;
    }
    .houseImg {
      width: 320px;
      @supports (-moz-appearance: none) {
        width: 300px;
      }
      img {
        border-radius: 20px 20px 0 0;
      }
    }
    .tag {
      z-index: 2;
      background-color: rgba(255, 255, 255, 0.968);
      border-radius: 50%;
      height: 30px;
      width: 30px;
      left: 1%;
      &.price {
        -webkit-text-stroke: black;
        -webkit-text-stroke-width: 0px;
        stroke-width: 222px;
        padding-block:20px;
        top: -2.1%;
        left: 20%;
        width: 200px;
        border-radius: 10px;
        color: fn.color("wheat");
        font-family: fn.font("price2");
        font-weight: bold;
        font-size: 30px;
        font-optical-sizing: auto;
        background-color: rgb(255, 255, 255);
      }
    }
    &:after {
      width: 50%;
    }
    &:hover {
      transform: scale(0.97);
      @include mx.transition(300ms, ease-in);
      &::after {
        background-color: white;
        width: 0%;
        height: 0%;
        animation: drawBorder 0ms ease-in-out, wobbleEffect 0s linear;
      }
    }
  }
}

