.single_card {
  box-shadow: 1px 2px 2px 1px rgba(69, 12, 12, 0.073);
  font-family: fn.font("list");
  gap: 10px;
  margin-inline: auto;
  width: 100%;
  background-color: fn.color("white");
  padding: 12px 12px;
  border-radius: 10px;
  @include mx.flex(column, center, flex-start);
  .header {
    width: 100%;
    margin-inline: auto;
    padding-inline: 2px;

    .house {
      position: relative;
      .arrow {
        cursor: pointer;
        border-radius: 50%;
        width: 60px;
        height: 60px;
        background-color: rgba(177, 186, 186, 0.568);
        z-index: 2;
        position: absolute;
        top: 35%;
        @include mx.transition(300ms, ease-out);
        &:active {
          transform: scale(0.9);
          background-color: fn.color("darkblue");
        }
        img {
          border-radius: 50%;
        }
      }
      .left {
        left: 5%;
        @media (min-width: 750px) {
          left: 10%;
        }
      }
      .right {
        right: 5%;
        @media (min-width: 750px) {
          right: 10%;
        }
      }
      .item {
        position: relative;
        padding-top: 56.25%;
        width: 200px;
        height: 10em;
        img {
          border-radius: 20px;
          display: block;
          margin-inline: auto;
          @media (min-width: 750px) {
            margin-inline: 1px;
          }
        }
      }
    }
    .location {
      padding-left: 6px;
      margin-top: 10px;
      font-size: 24px;
      font-weight: bold;
      word-wrap: break-word;
      white-space: pre-wrap;
    }
    .address {
      width: 100%;
      padding-left: 6px;
      text-wrap: balance;
      word-wrap: break-word;
      white-space: pre-wrap;
      position: relative;
      margin-top: 6px;
      opacity: 60%;
    }
  }
  .body {
    width: 100%;
    .home_details {
      position: relative;
      width: 100%;

      gap: 10px;
      flex-wrap: wrap;
      @include mx.flex(row, center, center);
      div {
        flex: 1;
        gap: 4px;
        @include mx.flex(row, center);
        @media (min-width: 500px) {
          @include mx.flex(row, center, center);
        }
        span {
          white-space: nowrap;
        }
      }
    }
  }
}

.singleCardCon2 {
