//Listing Form

.galleryImagesContainer {
  flex-wrap: wrap;
  width: 100%;
  gap: 10px;
  margin: 10px auto;
  @include mx.flex(row, center, center);
  .galleryImages {
    width: fit-content;
    border-radius: 10px;
  }
}

.mainImage {
  position: relative;
  margin-top: 25px;
  margin-inline: auto;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 8px;
  }
  .deleteLoader {
    position: absolute;
    top: 20%;
    left: 30%;
    z-index: 1;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .clickable {
    position: absolute;
    cursor: pointer;
    width: 30px;
    border-radius: 50%;
    top: -25%;
    right: 0;
  }
}

.galleryImages {
  @extend .mainImage;
  .clickable {
