    margin-top: 0;
    margin-left: 20px;
    margin-bottom: 20px;
  }
}
.form_item {
  width: 70%;
  gap: 4px;
  @include mx.flex(column, flex-start, space-between);
  @media (min-width: 1024px) {
    width: auto;
  }
}

.form_group.main_image,
.form_group.gallery {
  @include mx.flex(column, center, center);
}

.form_group.amenities {
  .items {
    width: 100%;
    @include mx.flex(row, center, center);

    div {
      width: 100%;
      @include mx.flex(column, center, center);
    }
  }
  input {
    width: 60px;
    text-align: center;
    &[type="number"]::-webkit-outer-spin-button,
    &[type="number"]::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
