}

.form {
  &.listing {
    .form_group {
      width: 100%;
    }
    .directional {
      width: 300px;
      margin-top: 20px;
      margin-inline: auto;
    }
    .darkblueBtn {
      width: 210px;
      padding: 8px;
      @include mx.center();
    }
    @media (min-width: 1200px) {
      .form_group.submit {
        margin-top: -86px;
      }
      display: grid;
      width: 100%;
      row-gap: 10px;
      grid-template-columns: repeat(2, 1fr);
    }
  }
}

.verify {
  gap: 20px;
  margin-top: 70px;
  margin-bottom: 6px;
  width: 100%;
  margin-inline: auto;
  @include mx.flex(column, center, center);
  .resend {
    cursor: pointer;
    text-decoration: underline;
  }

  form {
    @extend .verify;
    margin-top: 0;
  }
  button {
    width: 200px;
  }
  p {
    word-wrap: break-word;
    white-space: pre-wrap;
    width: 80%;
    margin-inline: auto;
    text-align: center;
  }
  .inputContainer {
    gap: 10px;
    width: 100%;
    @include mx.flex(row, center, center);
  }
  .otp-input {
    width: 60px;
    height: 60px;
    text-align: center;
    font-size: 20px;
    border-radius: 5px;
    border: none;
    outline: 1.5px solid rgba(0, 0, 0, 0.792);
    background-color: rgba(101, 100, 100, 0.151);
    color: rgba(54, 54, 54, 0.692);
    font-family: fn.font("headers");
    @include mx.transition(300ms, ease-out);
  }
  .otp-input:focus {
    outline: none;
    border: 2px solid fn.color("darkblue");
    background-color: fn.color("white");
    color: fn.color("darkblue");
    @include mx.transition(300ms, ease-out);
  }
  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}

.loginFirst {
  margin: 180px auto;
  width: 100%;
  @include mx.center();
}
